*{
    margin: 0px;
}

.header{
    display: flex;
    background-color: #6B007C;
    justify-content: space-between;
    margin-bottom: -8px;
 /* background-image: linear-gradient(to bottom, #6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C,#6B007C, #009060); */
}
.logo{
    margin-left: 2%;
    height: 8vh;
}

.nav{
    display: flex;
    justify-content: space-between;
    width: 18%;
    font-size: 15px;
    align-items: center;
    font-family: 'Inter', sans-serif;
}

a{
    color: inherit;
    text-decoration: none;
}

.btns{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
    margin-right:5%;
    /* height: 40%; */
}

.login ,.register {
    /* background-color: red; */
    color: #fff;
    height: 60%;
    width: 33%;
    font-family: 'Inter', sans-serif;
    /* font-weight: bold; */
    border-radius: 10px;
    border: none;
    background-color: #9911C9;
}

